var initializeVimeoPostersByControllers = function initializeVimeoPostersByControllers() {
  observer('[data-modal-dialog-iframe^="https://player.vimeo.com/video/"]', {
    inRange: function inRange(placeholder) {
      var url = placeholder.getAttribute("data-modal-dialog-iframe");
      var img = placeholder.getAttribute("data-src-img");
      if (!img) return;
      var id = vimeoManager.getIdByUrl(url);
      if (!id) return;
      var attributes = getJSONByElementAttribute(placeholder, "data-attr");
      var min = 400;
      var width = attributes.width > min ? attributes.width : min;
      var height = attributes.height > min ? attributes.height : min;
      vimeoManager.generatePosterImage(id, {
        width: width.toString(),
        height: height.toString() // quality: "100"

      }).then(function (posterImgUrl) {
        placeholder.setAttribute("data-src-img", posterImgUrl);
      }).finally(function () {
        placeholder.classList.add("vimeo-player-button");
        var playIconElement = createElement("span", {
          class: "vimeo-player-button__play"
        });
        placeholder.insertAdjacentElement("afterbegin", playIconElement);
        new LoadItem(placeholder).load(function () {
          return vimeoManager.createoEmbedById(id).then(function (response) {
            var metadata = JSON.parse(response.response);
            placeholder.setAttribute("data-modal-dialog-title", metadata.title);
            var img = placeholder.querySelector("img");
            if (!img) return;
            img.alt = metadata.title;
          });
        });
      });
    }
  });
  pubsub.subscribe("captureBody", function (elements) {
    var modalDialogContainers = elements.filter(function (element) {
      return element.classList.contains("modal-dialog--video");
    });
    modalDialogContainers.forEach(function (modalDialogContainer) {
      var iframe = modalDialogContainer.querySelector("iframe");
      if (!iframe) return;
      setElementAttributes(iframe, {
        allowfullscreen: "allowfullscreen",
        webkitallowfullscreen: "webkitallowfullscreen",
        allow: "fullscreen"
      });
      iframe.setAttribute("src", iframe.src);
      var player = vimeoManager.createPlayerByIframe(iframe);
      var captureContainer = new CaptureElement(modalDialogContainer);
      captureContainer.subscribe("attributes", function (record) {
        if (record.attributeName === "hidden" && modalDialogContainer.hasAttribute("hidden")) {
          player.pause();
        }
      });
    });
  });
};

