var pauseVimeoPlayersByElement = function pauseVimeoPlayersByElement(element) {
  if (!element) return;
  var candidates = element.querySelectorAll("iframe[src^=\"https://player.vimeo.com/video/\"]");
  if (!candidates) return;
  Array.from(candidates).forEach(function (candidate) {
    var player = elementVimeoPlayerRepository.get(candidate);
    if (!player) return;
    player.pause();
  });
};

